.content {
  background-color: #fff;
  border: 1px solid #000;
  border-radius: 24px;
}
.uploader_profile_img {
  width: 10%;
  padding-bottom: 10%;
  background-color: #000;
  border-radius: 50%;
  margin-right: 8px;
  position: relative;
  overflow: hidden;
}
.uploader_profile_img_img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.uploader_level {
  margin-right: 8px;
  padding: 0px 12px;

  border-radius: 18px;
  gap: 10px;
  background-color: #ff8b00;
  color: #fff;

  font-family: Pretendard;
  font-size: 12px;
  font-weight: 500;
  line-height: 22px;
  letter-spacing: 0em;
  text-align: left;
}

.uplaoder_name {
  font-family: Pretendard;
  font-size: 16px;
  font-weight: 600;
  line-height: 22px;
  letter-spacing: 0em;
  text-align: left;
}

.btn {
  border: 1px solid #c2c2c2;
  padding: 8px 10px;
  border-radius: 8px;
  margin-right: 4px;
}
.btn:hover {
  background-color: #0089ec;
}

.btn_like_active {
  color: red;
  background-color: red;
}

.photo_img {
  width: 100%;
  height: 45vh;
  display: flex;
  justify-content: center;
  align-items: start;
  overflow: hidden;
  border-radius: 16px;
  margin-bottom: 8px;
  border: 1px solid black;
}

.tag {
  padding: 2px 12px;
  border-radius: 18px;
  border: 1px solid #c2c2c2;
  gap: 10px;

  font-family: Pretendard;
  font-size: 14px;
  font-weight: 500;
  line-height: 24px;
  letter-spacing: 0em;
  text-align: left;
  margin-right: 4px;
}

.sentence_p {
  font-family: Pretendard;
  font-size: 16px;
  font-weight: 600;
  line-height: 30px;
  letter-spacing: 0em;
  text-align: left;
  color: #111111;
}

.commentContent {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin-bottom: 8px;
}

.commentText {
  height: 22%;
  border-radius: 8px;
  border: 1px solid gray;
  font-family: Pretendard;
  font-size: 14px;
  font-weight: 500;
  line-height: 24px;
  letter-spacing: 0em;
  text-align: left;
}

.commentBtn {
  width: 96px;
  height: 32px;
  border-radius: 24px;
  color: #fff;
  background-color: #000;
  font-family: Pretendard;
  font-size: 13px;
  font-weight: 500;
  line-height: 30px;
  letter-spacing: 0em;
}

.icons {
  width: 34px;
}
